import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';

import {
    Card,
    CardTitle,
    CardBody,
} from "reactstrap";

function AddLawyer() {
    const [validated, setValidated] = useState(false);
    const [states, setStates] = useState(["Maharashtra", "karanataka", "Goa"]);
    const [cities, setCities] = useState(["Nipani", "Pune", "Banglore"]);
    const [languages, setLanguage] = useState(["English", "Hindi", "Marathi"])

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }

        setValidated(true);
    };

    return (
        <Row>
            <Col>
                {/* --------------------------------------------------------------------------------*/}
                {/* Card-1*/}
                {/* --------------------------------------------------------------------------------*/}
                <Card>
                    <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                        <i className="bi bi-bell me-2"> </i>
                        Add Lawyer
                    </CardTitle>
                    <CardBody>

                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                    <Form.Label>What is your full name?</Form.Label>
                                    <Form.Control
                                        required
                                        type="text"
                                        placeholder="Enter your full name"
                                    />
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                    <Form.Label>What is your Email ID?</Form.Label>
                                    <Form.Control
                                        required
                                        type="email"
                                        placeholder="Enter your full email"
                                    />
                                </Form.Group>

                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                    <Form.Label>What is your mobile number?</Form.Label>
                                    <Form.Control
                                        required
                                        type="number"
                                        placeholder="Enter your mobile number"
                                    />
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                    <Form.Label>Which state do you live in?</Form.Label>
                                    <Form.Control required as="select" type="select" name="payment_method"  >
                                        <option value="">--- Select your State ---</option> 
                                        {
                                            states.map((state, index) => {
                                                return <option key={index} >{state}</option>
                                            })
                                        }
                                    </Form.Control>

                                </Form.Group>

                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                    <Form.Label>Whch city do you live in?</Form.Label>

                                    <Form.Control required as="select" type="select"   >
                                        <option value="">--- Select your City ---</option> 
                                        {
                                            cities.map((city, index) => {
                                                return <option key={index} required>{city}</option>
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="formFile" className="mb-3">
                                    <Form.Label>Please upload your Resume/CV</Form.Label>
                                    <Form.Control required type="file" />
                                </Form.Group>
                            </Row>
                            <Row className="mb-2">
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                    <Form.Label>Please Select Language?</Form.Label>

                                    <Form.Control required as="select" type="select"   >
                                        <option value="">--- Select your language ---</option> 
                                        {
                                            languages.map((language, index) => {
                                                return <option key={index} >{language}</option>
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                            </Row>
                            <Row className="mb-2 mt-3">
                                <Form.Group as={Col} md={{ span: 4, offset: 1 }} controlId="validationCustom01">
                                <Button type="submit">Submit</Button>
                                </Form.Group>
                            </Row>


                            
                        </Form>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
}
export default AddLawyer;

